.clearfix {
  *zoom: 1;
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
  line-height: 0;
}
.clearfix:after {
  clear: both;
}
.form-wizard .steps {
  margin-bottom: 15px;
}
.form-wizard .steps li {
  padding: 0 15px;
}
.form-wizard .steps li.active .step,
.form-wizard .steps li.done .step {
  background: none;
}
.form-wizard .steps li.active .step .number,
.form-wizard .steps li.done .step .number {
  color: #fff;
}
.form-wizard .steps li.active .step {
  color: #222222;
  font-weight: 600;
}
.form-wizard .steps li.active .step .number {
  background-color: #4d7496;
  border-color: #3c5a74;
}
.form-wizard .steps li.done .step {
  color: #94b86e;
  font-weight: 300;
}
.form-wizard .steps li.done .step .number {
  background-color: #94b86e;
  border-color: #7ba350;
}
.form-wizard .steps li.done .step i {
  display: inline;
}
.form-wizard .step {
  color: #626262;
}
.form-wizard .step:hover {
  background-color: #f9f9f9;
}
.form-wizard .step .number {
  background-color: #ececec;
  border: 1px solid #d1d1d1;
  color: #555555;
  display: inline-block;
  text-align: center;
  font-size: 15px;
  font-weight: 600;
  padding: 8px;
  margin-right: 10px;
  height: 40px;
  width: 40px;
}
.form-wizard .step i {
  display: none;
}
@media (max-width: 979px) {
  .form-wizard .step {
    text-align: left;
  }
}



